<template>
  <div class="news">
    <scroll ref="scroll" class="news-content" :data="newsList" :pullup="true" v-on:scrollToEnd="_getMoreData">
      <div>
        <div v-if="ads.length" class="slider-wrapper" ref="sliderWrapper">
          <slider :interval="2000" :autoPlay="true" :loop="true">
            <div v-for="item in ads">
              <a :href="item.linkUrl">
                <!-- 使用css类needsclick，解决fackclick与better-scroll点击事件冲突问题,@load="loadImage"解决轮播图异步加载，scroll无法计算该部分的大小，导致无法滚动到最底部 -->
                <img class="needsclick" @load="loadImage" :src="item.picUrl">
              </a>
            </div>
          </slider>
        </div>
        <h2>最火资讯</h2>
        <ul class="title">
          <router-link v-for="(item,index) in newsList" to="/detail" tag="li" :key="item.id" :id="item.id">
            <div class="left"><img v-lazy="item.litpic" alt=""></div>
            <div class="right">
              <h3 v-text="item.title"></h3>
              <p v-text="item.description"></p>
            </div>
            <span v-html="item.comments_total+'评论'"></span>
          </router-link>
        </ul>
      </div>
      <loading v-show="!newsList.length"></loading>
    </scroll>
  </div>
</template>

<script>
/**
*
* better-scroll坑点
* 1. 局部滚动区域要放在scroll标签的子div里面
* 2. 如果局部滚动区域数据是动态获取的，要把数据通过data属性传到scroll组件进行滚动高度的计算
* 3. 使用css类needsclick，解决fackclick与better-scroll点击事件冲突问题
* 4. 最外层即news一定要使用fixed定位，scroll要设置height为100%，overflow为hidden
* 5. scroll子组件使用emit触发父组件的方法时，要用v-on绑定不能用@
* 6. 因为图片是异步加载的，为了保证scroll对滚动区域的大小计算准确，img标签需要预先设置好宽高或css样式，width="60" height="60"
* 7. 原理是最外层的父元素宽高固定，当scroll计算好滚动区域的宽高后，如过大于外层父元素的宽高才会产生滚动，否则不能产生局部滚动，异步的图片尺寸宽高要用固定值，不要百分比
* 8. 为什么父元素要使用fixed定位，因为BScroll要求要父元素的高度要固定，由子元素的高度去撑开它
**/
import Slider from 'components/slider/slider'
import Scroll from 'components/scroll/scroll'
import Loading from 'components/loading/loading'
import {getNews} from 'api/news'
  export default {
  	data() {
  		return {
        ads: [{
         linkUrl: 'http://www.baidu.com',
         picUrl: require('common/images/1.jpg')
        },{
         linkUrl: 'http://www.baidu.com',
         picUrl: require('common/images/2.jpg') 
        },{
         linkUrl: 'http://www.baidu.com',
         picUrl: require('common/images/3.jpg')
        },{
         linkUrl: 'http://www.baidu.com',
         picUrl: require('common/images/4.jpg')
        }],
        newsList: [],
        curPage: 1 // 当前页
  		}
  	},
  	created() {
      this._getNews(this.curPage)
  	},
  	mounted() {
      setTimeout(() => {
        this.$store.commit('increment')
      },20)
  	},
  	methods: {
      _getMoreData() {
        this._getNews(++this.curPage)
      },
      loadImage() {
        // 因为图片是异步加载的，div的大小是通过图片撑起的，所以一开始scroll无法计算出图片的大小，
        if (!this.checkloaded) {
          this.checkloaded = true
          // 需要等图片异步加载完后调用scroll的refresh方法重新计算滚动区域的大小
          this.$refs.scroll.refresh()
        }
      },
      _getNews(page) {
        getNews(page).then((res) => {
          console.log(res)
          if(res.statusText === 'OK') {
            this.newsList = this.newsList.concat(res.data.list.articles)
            // this.newsList = res.data.list.articles
            this.curPage = res.data.page
          }
        })
      }
  	},
  	computed: {

  	},
  	watch: {

  	},
    components: {
      Slider,
      Scroll,
      Loading
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
.news
  position: fixed
  width: 100%
  top: 76px
  bottom: 0
  .news-content
    height: 100%
    overflow: hidden
    .slider-wrapper
      position: relative
      width: 100%
      overflow: hidden
    h2
      padding: .5rem 0
      color: $color-g
      border-top: 1px solid $color-border
      border-bottom: 1px solid $color-border
    ul.title
      li
        display: flex
        justify-content: center
        align-items: center
        padding: .5rem 0  
        border-bottom: 1px solid $color-border
        position: relative
        .left
          flex: 2
          margin-left: .5rem
          img
            width: 100%
            height: 3.8rem
        .right
          flex: 5
          padding: 0 .5rem
          text-align: left
          h3
            font-size: .9rem
            line-height: 1.5rem
          p
            font-size: .6rem
            line-height: 1rem
            color: #8a8787
        span
          position: absolute
          bottom: 5px
          right: 5px
          font-size: .6rem
          color: #8a8787
      li:hover
        color: $color-g
</style>


